@import '../../fonts/fonts.scss';
@import '../../scss/null.scss';
@import '../01_header/_header.scss';

#root {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
  font-family: 'Rubik', sans-serif;
  color: #45515F;
  font-weight: 400;

  button, a {

    &:hover {
      animation: pulsate 0.3s ease-in-out;
  
      @keyframes pulsate {
        0% {
          box-shadow: inset 0 0 70px orange;
          color: black;
          font-size: 300;

          @media (min-width: 1000px) {
            border-radius: 10px;
          }
        }
      }
    }
  }

  button {
    color: #45515F;
  }
}